@import url("https://fonts.googleapis.com/css?family=Nosifer");
@import url("https://fonts.googleapis.com/css?family=Lato");
@import url("https://fonts.googleapis.com/css?family=Chakra+Petch");
.img-con {
  overflow: hidden !important;
  position: relative;
}

#portfolio-item {
  -webkit-box-shadow: 0px 10px 26px 0px rgba(120, 120, 120, 1);
  -moz-box-shadow: 0px 10px 26px 0px rgba(120, 120, 120, 1);
  box-shadow: 0px 10px 26px 0px rgba(120, 120, 120, 1);
}
.hidden {
  position: absolute;
  top: 0;
  left: 0;
  background: gray;
  width: 100%;
  height: 800px;
  margin-left: -900px;
  background: #16222a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3a6073,
    #16222a
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3a6073, #16222a);

  transition: all 0.4s;
}
.img-con:hover .hidden {
  cursor: pointer;
  margin: 0;
}

/* Progress.js */
.prog-con {
  position: relative;
  margin-top: 50px;
}

.ico {
  position: absolute;
  left: -40px;
  top: -7px;
}

/* Header.js */
header {
  min-height: 100vh;
  background: #16222a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3a6073,
    #16222a
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #3a6073,
    #16222a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
header {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 866px) {
  header h1 {
    font-size: 30px !important;
  }
}

.card {
  min-width: 100%;
}

/* Fonts */
.font-end-p {
  font-family: "Nosifer", cursive;
}

.port-details {
  font-family: "Lato", sans-serif;
  font-size: 20px;
}

.project-title {
  font-family: "Chakra Petch", sans-serif;
}
header p {
  font-family: "Chakra Petch", sans-serif;
  font-size: 20px;
}
